@use './base';
@use "./components";
@use "./base/vars";

.uikit-block {
    display: flex;
    flex-direction: column;
    padding: 1rem;

}
.color-block {
    position: relative;
    padding: 1rem 2rem;
    max-width: 50%;
    font-family: vars.$mainFontRegular;
    font-size: vars.$h4Size;
    &_primary-color{
        background-color: vars.$primaryColor;
        color: vars.$white;
        &::after{
            position: absolute;
            right: 1rem;
            font-family: vars.$mainFontBold;
            text-transform: uppercase;
            content: "#1a6eff";
        }
    }
    &_secondary-color{
        background-color: vars.$secondaryColor;
        color: vars.$white;
        &::after{
            position: absolute;
            right: 1rem;
            font-family: vars.$mainFontBold;
            text-transform: uppercase;
            content: "#60a2ff";
        }
    }
    &_light-color{
        background-color: vars.$lightColor;
        &::after{
            position: absolute;
            right: 1rem;
            font-family: vars.$mainFontBold;
            text-transform: uppercase;
            content: "#e7effe";
        }
    }
    &_borders-color{
        background-color: vars.$borderColor;
        &::after{
            position: absolute;
            right: 1rem;
            font-family: vars.$mainFontBold;
            text-transform: uppercase;
            content: "#d7deef";
        }
    }
    &_dark-color{
        background-color: vars.$darkColor;
        color: vars.$white;
        &::after{
            position: absolute;
            right: 1rem;
            font-family: vars.$mainFontBold;
            text-transform: uppercase;
            content: "#1e2736";
        }
    }
    &_grey-color{
        background-color: vars.$greyColor;
        color: vars.$white;
        &::after{
            position: absolute;
            right: 1rem;
            font-family: vars.$mainFontBold;
            text-transform: uppercase;
            content: "#434c5b";
        }
    }
    &_lightgrey-color{
        background-color: vars.$lightGreyColor;
        color: vars.$white;
        &::after{
            position: absolute;
            right: 1rem;
            font-family: vars.$mainFontBold;
            text-transform: uppercase;
            content: "#646d83";
        }
    }
}

.divider {
    height: 20px;
}

// main {
//     background-color: vars.$lightColor;
// }

.wrapper {
    margin: 0 auto;
    max-width: 1280px;
    &--wided{
        max-width:1800px;
    }
}

.badge {
    top: -30%;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    padding: 1.3rem 3rem;
    background-color: vars.$lightColor;
    border-radius: 3em;
    font-size: vars.$badgeFontSize;
    font-family: vars.$mainFontBold;
    color: vars.$primaryColor;
    text-transform: uppercase;
}

// Exclusive Offer Badge
.exclusive-offer{
    outline: 1px red solid;
    
    &__header{
        position: relative;
    }
    &__title{
        text-align: center;
    }
    &__badge{
        position: absolute;
        @extend .badge;
        padding: 0.8 1.5rem;
        &::before {
            content: url(/src/graphics/png/exclusive-badge-before.png);
        }
    }
    &__cards {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: auto auto auto;
        grid-template-areas:
        "main tax transfer"
        "main months reports"
        "main cta cta";
        gap:2.4rem;
    }
}
#main {grid-area: main;}
#tax {grid-area: tax;}
#transfer {grid-area: transfer;}
#months {grid-area: months;}
#reports {grid-area: reports;}
#cta {grid-area: cta;}


// Services
.services {
    display: flex;
    flex-direction: column;
    padding-block: 20rem;
    gap: 3rem;
    
    &__title, &__subtitle {text-align: center}
    &__subtitle {
        color: vars.$greyColor;
    }
    &__header {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 3rem;
    }
    &__badge{
        @extend .badge;
    }

    &__grid{
        --min: 800px;
        display: grid;
        gap: 2.5rem 3rem;
        grid-template-columns:  repeat(auto-fit, minmax(var(--min), 1fr));
        align-items: stretch;
    }
}

// Logistic
.logistic {
    &__badge{
        @extend .badge;
    }
}

// Contact
.contact {
    display: grid;
    grid-template-columns: 1fr 1fr;
    
}